import { ChangeDetectorRef, Component } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { ProductService } from 'src/app/shared/services/product.service';

@Component({
  selector: 'app-search-product',
  templateUrl: './search-product.component.html',
  styleUrl: './search-product.component.scss'
})
export class SearchProductComponent {

  public product_list: any[] = [];

  //tamao de paginacion e inicio
  public page = 1
  public pageSize = 10
  public layoutView: string = 'grid-view';
  public grid: string = 'col-xl-2 col-md-6';

  private routerSubscription!: Subscription;

  constructor(private productService: ProductService) { }

  ngOnInit(): void {
    // Suscribirse al servicio para recibir actualizaciones de la lista de productos
    this.productService.productList$.subscribe(products => {
      this.product_list = products;
    });
  }
}
