import { Component, OnInit, Output, Input, EventEmitter, Inject, PLATFORM_ID } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';
import { Options } from 'ngx-slider-v2';

@Component({
  selector: 'app-price',
  templateUrl: './price.component.html',
  styleUrls: ['./price.component.scss']
})
export class PriceComponent implements OnInit {

  // Using Output EventEmitter
  @Output() priceFilter: EventEmitter<any> = new EventEmitter<any>();

  // define min, max and range
  @Input() min: number;
  @Input() max: number;

  public collapse: boolean = true;
  public isBrowser: boolean = false;

  public price: any;

  // Configuracin del slider
  options: Options = {
    floor: 0,
    ceil: 10000,
    step: 1, // Puedes ajustar el tamao del paso si es necesario
    translate: (value: number): string => `\$${value}` // Opcional: para mostrar el valor en formato de moneda
  };

  constructor() {
  }

  ngOnInit(): void { }

  // Mtodo para manejar cambios en los valores del slider
  onValueChange(event: any): void {
    if (this.min < this.options.floor) {
      this.min = this.options.floor;
    }
    if (this.max > this.options.ceil) {
      this.max = this.options.ceil;
    }
  }
}
