import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import * as _ from 'lodash'
import { ProductoService } from 'src/app/shared/services/productos/producto.service';

@Component({
  selector: 'app-mochilas-left-sidebar',
  templateUrl: './mochilas-left-sidebar.component.html',
  styleUrls: ['./mochilas-left-sidebar.component.scss']
})
export class MochilasLeftSidebarComponent implements OnInit {

  public grid: string = 'col-xl-3 col-md-6';
  public layoutView: string = 'grid-view';
  public all_products: any[] = [];
  public products: any[] = [];
  public brands: any[] = [];
  public colors: any[] = [];
  public size: any[] = [];
  public minPrice: number = 0;
  public maxPrice: number = 1200;
  public tags: any[] = [];
  public category: string;
  public pageNo: number = 1;
  public paginate: any = {}; // Pagination use only
  public sortBy: string; // Sorting Order
  public mobileSidebar: boolean = false;
  public loader: boolean = true;
  public finished: boolean = false  // boolean when end of data is reached
  public addItemCount = 8;

  //Array para productos
  public product_list: any[] = [];

  listaVacia: any = undefined;

  //tamao de paginacion e inicio
  public page = 1
  public pageSize = 20

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    public productoService: ProductoService
  ) { }

  ngOnInit(): void {
    this.productoService.currentProductList.subscribe((products: any[]) => {
      this.product_list = products;
    });

    this.listProductosMochilas();
  }


  // Change Grid Layout
  updateGridLayout(value: string) {
    this.grid = value;
  }

  // Change Layout View
  updateLayoutView(value: string) {
    this.layoutView = value;
    if (value == 'list-view')
      this.grid = 'col-lg-12';
    else
      this.grid = 'col-xl-3 col-md-6';
  }

  // Mobile sidebar
  toggleMobileSidebar() {
    this.mobileSidebar = !this.mobileSidebar;
  }


  onHover(menuItem) {
    if (window.innerWidth > 1200 && menuItem) {
      document.getElementById('unset').classList.add('sidebar-unset')
    } else {
      document.getElementById('unset').classList.remove('sidebar-unset')
    }
  }

  //Listar todos los productos de la categoira Mochilas
  listProductosMochilas() {
    const categoriaId = 9 //Pertenece el id de Mochilas en la base de datos

    this.productoService.getProductosCategoria(categoriaId).subscribe(
      (data: any) => {
        this.product_list = data.map((product: any) => {
          return {
            ...product,
            img_url: product.imagen_producto && product.imagen_producto.length > 0 ? product.imagen_producto[0].imagen_url : 'path/to/default/image.jpg'
          };
        });
        this.listaVacia = undefined;
      },
      err => {
        this.listaVacia = err.error.message;
        console.log(err.error.message)
      }
    );
  }

}
