import { Component, OnInit, OnDestroy } from '@angular/core';

@Component({
  selector: 'app-countdown',
  templateUrl: './countdown.component.html',
  styleUrls: ['./countdown.component.scss']
})
export class CountdownComponent implements OnInit, OnDestroy {

  private intervalId: any;
  public days: number = 0;
  public hours: number = 0;
  public minutes: number = 0;
  public seconds: number = 0;

  constructor() {}

  ngOnInit(): void {
    // Inicializa el contador
    this.startCountdown();

    // Actualiza cada segundo
    this.intervalId = window.setInterval(() => {
      this.updateCountdown();
    }, 1000);
  }

  ngOnDestroy(): void {
    if (this.intervalId) {
      clearInterval(this.intervalId);
    }
  }

  // Mtodo para iniciar el contador con base en la fecha y hora actual hasta la medianoche
  startCountdown(): void {
    const now = new Date();
    const midnight = new Date(now.getFullYear(), now.getMonth(), now.getDate() + 1, 0, 0, 0); // La siguiente medianoche
    const timeLeft = midnight.getTime() - now.getTime(); // Diferencia entre ahora y la medianoche

    this.calculateTimeLeft(timeLeft);
  }

  // Mtodo para actualizar el contador
  updateCountdown(): void {
    const now = new Date();
    const midnight = new Date(now.getFullYear(), now.getMonth(), now.getDate() + 1, 0, 0, 0); // Prxima medianoche
    const timeLeft = midnight.getTime() - now.getTime(); // Diferencia entre ahora y medianoche

    if (timeLeft <= 0) {
      this.startCountdown(); // Reinicia el contador para el nuevo da
    } else {
      this.calculateTimeLeft(timeLeft); // Actualiza la cuenta regresiva
    }
  }

  // Mtodo para calcular el tiempo restante y asignarlo a das, horas, minutos, segundos
  calculateTimeLeft(timeLeft: number): void {
    this.days = Math.floor(timeLeft / (1000 * 60 * 60 * 24)); // Aunque ser siempre 0 porque es el mismo da
    this.hours = Math.floor((timeLeft % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    this.minutes = Math.floor((timeLeft % (1000 * 60 * 60)) / (1000 * 60));
    this.seconds = Math.floor((timeLeft % (1000 * 60)) / 1000);
  }
}
