import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ComentariosService {

  private comentario_URL = environment.comentario_URL

  constructor(private httpClient: HttpClient) { }

  //Solicitud Crear Comentario Sin Producto
  public createComentario(formData: FormData): Observable<any> {
    return this.httpClient.post(this.comentario_URL + 'comentarios-simple', formData);
  }

  //Solicitud Crear Comentario Con Producto
  public createComentarioProducto(formData: FormData): Observable<any> {
    return this.httpClient.post(this.comentario_URL, formData);
  }

  //Solicitud Listar Comentarios del Producto
  public getComentarioProductos(productoId: number): Observable<any> {
    return this.httpClient.get<any>(`${this.comentario_URL}` + 'producto/' + productoId)
  }
}
