import { Component, OnInit, AfterViewInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ProductDetailsMainSlider, ProductDetailsThumbSlider } from '../../../shared/data/slider';
import { Product } from '../../../shared/classes/product';
import { ProductService } from '../../../shared/services/product.service';
import { ProductoService } from 'src/app/shared/services/productos/producto.service';
import { environment } from 'src/environments/environment';
import { Modal } from 'bootstrap';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ComentariosService } from 'src/app/shared/services/comentarios/comentarios.service';
import { ToastrService } from 'ngx-toastr';
import { CreateComentarioDto } from 'src/app/shared/classes/comentarios.dto';
import { CartModalComponent } from 'src/app/shared/components/modal/cart-modal/cart-modal.component';


@Component({
  selector: 'app-three-column',
  templateUrl: './three-column.component.html',
  styleUrls: ['./three-column.component.scss']
})
export class ThreeColumnComponent implements OnInit, AfterViewInit {
  

  //public product: Product = {};
  public counter: number = 1;
  public activeSlide: any = 0;
  public selectedSize: any;
  public active = 1;

  //Atributos para el producto recibido
  id_producto: number
  nombre_producto: string
  descripcion_producto: string
  precio_producto: number
  precio_oferta_producto: number
  //Array para las imagenes del producto
  producto_imagenes: any[] = [];
  //Array para el inventario
  producto_inventario: any[] = [];

  //Array Comentarios
  comentarios_producto: CreateComentarioDto[]

  urlApi = environment.apiBaseUrl


  //Array para el producto
  public product_list: any[] = [];
  listaVacia: any = undefined;

  cartModal: boolean = false; // Default False

  //Formulario Comentario
  comentarioForm: FormGroup;
  listaVaciaComentario: string;


  @ViewChild("cartModal") CartModal: CartModalComponent;

  public ProductDetailsMainSliderConfig: any = ProductDetailsMainSlider;
  public ProductDetailsThumbConfig: any = ProductDetailsThumbSlider;
  private modal: Modal | undefined;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    public productoService: ProductoService,
    public productService: ProductService,
    private fb: FormBuilder,
    private toastrService: ToastrService,
    public comentarioServices: ComentariosService
  ) {
    this.comentarioForm = this.fb.group({
      com_nombre_cliente: ['', [Validators.required]],
      com_apellido_cliente: ['', [Validators.required]],
      com_comentario: ['', [Validators.required]],
      productoId: ['', [Validators.required]],  // Valor inicial
    });
  }

  ngAfterViewInit(): void {
    const modalElement = document.getElementById('imageModal') as HTMLElement;
    if (modalElement) {
      this.modal = new Modal(modalElement);
    }
  }


  ngOnInit(): void {
    this.id_producto = parseInt(localStorage.getItem('prod_id') || 'null', 10);
    this.getOneProducto();

    //Asignar el id del producto
    if (this.id_producto) {
      // Uso de patchValue para asignar id_producto al control productoId
      this.comentarioForm.patchValue({
        productoId: this.id_producto
      });
    }
    this.listarComentariosProducto();
  }

  //Modal Imagen
  openModal(imageUrl: string): void {
    const modalImage = document.getElementById('modalImage') as HTMLImageElement;
    modalImage.src = imageUrl;
    if (this.modal) {
      this.modal.show();
    }
  }

  closeModal(): void {
    if (this.modal) {
      this.modal.hide();
    }
  }

  // Obtener un producto, solicitud a la API
  getOneProducto() {
    if (this.id_producto) {
      this.productoService.getOneProducto(this.id_producto).subscribe(
        (data: any) => {
          this.product_list = {
            ...data,
            img_url: data.imagenes && data.imagenes.length > 0 ? data.imagenes[0].imagen_url : 'path/to/default/image.jpg'
          };
          //Asignando el producto a las variables
          this.nombre_producto = data.prod_nombre
          this.descripcion_producto = data.prod_descripcion
          this.precio_producto = data.prod_precio
          this.precio_oferta_producto = data.prod_precio_oferta
          this.producto_imagenes = data.imagenes
          this.producto_inventario = data.inventario
          this.listaVacia = undefined;
        },
        err => {
          this.listaVacia = err.error.message;
          console.log(err.error.message);
        }
      );
    }
  }

  // Obtener la cantidad disponible basada en la talla seleccionada
  getStockDisponible(): number {
    if (this.selectedSize) {
      const sizeInventory = this.producto_inventario.find(item => item.inv_talla === this.selectedSize);
      return sizeInventory ? sizeInventory.inv_cantidad_disponible : 0;
    } else {
      return this.producto_inventario.reduce((total, item) => total + item.inv_cantidad_disponible, 0);
    }
  }

  // Mtodo para verificar si existe al menos una talla en el inventario
  hasAvailableSize(): boolean {
    return this.producto_inventario.some(item => item.inv_talla && item.inv_talla.trim() !== '');
  }


  // Get Product Color
  Color(variants) {
    const uniqColor = []
    for (let i = 0; i < Object.keys(variants).length; i++) {
      if (uniqColor.indexOf(variants[i].color) === -1 && variants[i].color) {
        uniqColor.push(variants[i].color)
      }
    }
    return uniqColor
  }

  // Get Product Size
  // Mtodo para obtener la lista de tallas
  Size(variants: any[]): string[] {
    return [...new Set(variants.map(v => v.inv_talla))];
  }

  selectSize(size) {
    this.selectedSize = size;
    console.log(this.selectSize)
  }

  // Incrementar
  increment() {
    if (this.counter < this.getStockDisponible()) {
      this.counter++;
    }
  }
  // Decrement
  decrement() {
    if (this.counter > 1) this.counter--;
  }


  async addToCart(product: any, quantity?: number, selectedSize?: string) {
    if (this.selectedSize) {
      selectedSize = this.selectedSize
    }

    if (this.CartModal) {
      this.CartModal.openModal(product, quantity, selectedSize);
    } else {
      console.error('CartModal no est inicializado.');
    }
  }

  // Buy Now
  async buyNow(product: any, quantity?: number, selectedSize?: string) {

    // Verifica si el producto tiene tallas disponibles
    const hasSizes = product.inventario && product.inventario.some(inv => inv.inv_talla && inv.inv_talla.trim() !== '');

    // Si el producto tiene tallas disponibles, verifica que se haya seleccionado una talla
    if (hasSizes && !selectedSize) {
      // Muestra un mensaje de error si no se ha seleccionado una talla
      this.toastrService.warning('Por favor, selecciona una talla antes de continuar con la compra.', 'Talla Requerida', {
        timeOut: 3000,
        positionClass: 'toast-top-center',
      });
      return;
    }

    // Realizar la lgica para filtrar productos relacionados
    await this.productoService.getProductosCategoria(product.categoriaId).subscribe(response => {
      this.product_list = response.filter(items => items.prod_id !== product.prod_id);
    });

    // Aadir el producto al carrito con la talla seleccionada (si corresponde)
    const status = await this.productoService.addToCart(product, quantity, selectedSize);

    // Redirigir a la pgina de checkout si el producto se aade al carrito correctamente
    if (status) {
      this.router.navigate(['/shop/checkout']);
    }
  }


  // Add to Wishlist
  addToWishlist(product: any) {
    this.productService.addToWishlist(product);
  }

  //Solicitud Listar Comentarios Del Producto
  listarComentariosProducto() {
    this.comentarioServices.getComentarioProductos(this.id_producto).subscribe(
      data => {
        this.comentarios_producto = data
        this.listaVaciaComentario = undefined
      },
      err => {
        this.listaVaciaComentario = 'No hay comentarios para este producto'
      }
    )
  }




  //Solicitud Crear Comentario
  // Mtodo para manejar el envo del formulario
  onSubmit(): void {
    if (this.comentarioForm.invalid) {
      this.toastrService.error('Por favor, complete todos los campos requeridos.', 'Error', {
        timeOut: 3000,
        positionClass: 'toast-top-center',
      });
    }

    if (this.comentarioForm.valid) {
      const formData = this.comentarioForm.value;
      this.comentarioServices.createComentarioProducto(formData).subscribe(
        (data) => {
          this.toastrService.success(data.message, 'xito', {
            timeOut: 3000,
            positionClass: 'toast-top-center',
          });
          // Reiniciar los inputs
          this.comentarioForm.reset();
          //Cargar los nuevos Comentarios
          this.listarComentariosProducto()
          //Mostrar la lista de comentarios en el Nav
          this.active = 1

          this.id_producto = parseInt(localStorage.getItem('prod_id') || 'null', 10);
          //Asignar el id del producto
          if (this.id_producto) {
            // Uso de patchValue para asignar id_producto al control productoId
            this.comentarioForm.patchValue({
              productoId: this.id_producto
            });
          }

        },
        (error) => {
          const errorMessage = error.error?.message || 'Error al crear el producto';
          this.toastrService.error(errorMessage, 'Error', {
            timeOut: 3000,
            positionClass: 'toast-top-center',
          });
          console.error(error);
        }
      );
    }
  }
}
