import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Product } from '../../../../shared/classes/product';
import { ProductoService } from 'src/app/shared/services/productos/producto.service';

@Component({
  selector: 'app-grid',
  templateUrl: './grid.component.html',
  styleUrls: ['./grid.component.scss']
})
export class GridComponent implements OnInit {


  @Input() paginate: any = {};
  @Input() paginate2: any = {};
  @Input() layoutView: string = 'grid-view';
  @Input() sortBy: string = 'AZ';

  //Pedir seccion o Categoria para enviar id al backend
  @Input() seccionId: number
  @Input() categoriaId: number

  @Input() pageSize: number

  @Input() product_list: any[] = [];

  @Output() setGrid: EventEmitter<any> = new EventEmitter<any>();
  @Output() setLayout: EventEmitter<any> = new EventEmitter<any>();
  @Output() sortedBy: EventEmitter<any> = new EventEmitter<any>();

  constructor(private productoService: ProductoService) { }

  ngOnInit(): void {
  }

  setGridLayout(value: string) {
    this.setGrid.emit(value);  // Set Grid Size
  }

  setLayoutView(value: string) {
    this.layoutView = value
    this.setLayout.emit(value); // Set layout view
  }

  // Mtodo para obtener productos ordenados
  getSortedProducts(orderBy: string) {
    this.productoService.getProducts(orderBy, this.seccionId, this.categoriaId).subscribe((res: any[]) => {
      this.product_list = res;
      this.productoService.updateProductList(res); // Actualiza la lista en el servicio
    });
  }

  // Mtodo para manejar la seleccin de orden en el HTML
  sorting(event) {
    const selectedOrder = event.target.value;
    this.sortedBy.emit(selectedOrder);
    this.getSortedProducts(selectedOrder);  // Llama al servicio para obtener productos ordenados
  }
}
