import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class UbicacionService {

  private ubicacionUrl = environment.ubicacion_URL

  constructor(private httpClient: HttpClient) { }

  //Solicitar la lista de paises
  public getAllCountries(): Observable<any> {
    return this.httpClient.get<any>(this.ubicacionUrl + 'countries');
  }

  //Solicitar la lista de paises
  public getAllStateByCountrie(country_name: string): Observable<any> {
    return this.httpClient.get<any>(this.ubicacionUrl + 'countrie/states/' + country_name);
  }

  //Solicitar la lista de ciudades
  public getAllCityByState(city_name: string): Observable<any> {
    return this.httpClient.get<any>(this.ubicacionUrl + 'countrie/state/cities/' + city_name);
  }

}
