import { Component, OnInit } from '@angular/core';
import { UntypedFormGroup, UntypedFormBuilder, Validators } from '@angular/forms';
import { Observable, of, Subscription } from 'rxjs';
import { environment } from '../../../environments/environment';
import { OrdenService } from 'src/app/shared/services/orden/orden.service';
import { ProductoService } from 'src/app/shared/services/productos/producto.service';
import { UbicacionService } from 'src/app/shared/services/ubicacion/ubicacion.service';


@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.scss']
})
export class CheckoutComponent implements OnInit {

  public checkoutForm: UntypedFormGroup;
  public products: any[] = [];
  public paises: any[] = [];
  public departamentos: any[] = [];
  public ciudades: any[] = [];
  public payment: string = 'ePayco';
  public amount: any;
  totalItems$: Observable<number>;
  private cartSubscription: Subscription;

  epayco_public_key = environment.epayco_public_key
  urlFront = environment.urlFront


  tiposIdentificacion = [
    { value: 'PASAPORTE', label: 'Pasaporte' },
    { value: 'CEDULA', label: 'Cedula de Ciudadania' },
    { value: 'LICENCIA', label: 'Licencia de Conducir' },
    { value: 'NIE', label: 'Nmero de Identidad de Extranjero (NIE)' },
    { value: 'RUT', label: 'Registro Unico Tributario (RUT)' },
    { value: 'RFC', label: 'Registro Federal de Contribuyentes (RFC)' },
    { value: 'CURP', label: 'Clave Unica de Registro de Poblacin (CURP)' },
    { value: 'SSN', label: 'Nmero de Seguro Social (SSN)' },
  ];

  constructor(
    private fb: UntypedFormBuilder,
    public productoServices: ProductoService,
    private ordenService: OrdenService,
    private ubicacionService: UbicacionService
  ) {
    this.checkoutForm = this.fb.group({
      cli_tipo_identificacion: ['', [Validators.required, Validators.pattern('[a-zA-Z][a-zA-Z ]+[a-zA-Z]$')]],
      cli_identificacion: ['', [Validators.required, Validators.pattern('[0-9]+')]],
      cli_nombre: ['', [Validators.required, Validators.pattern('[a-zA-Z][a-zA-Z ]+[a-zA-Z]$')]],
      cli_apellido: ['', [Validators.required, Validators.pattern('[a-zA-Z][a-zA-Z ]+[a-zA-Z]$')]],
      cli_telefono: ['', [Validators.required, Validators.pattern('[0-9]+')]],
      cli_email: ['', [Validators.required, Validators.email]],
      dir_direccion: ['', [Validators.required, Validators.maxLength(50)]],
      dir_pais: ['', Validators.required],
      dir_ciudad: ['', Validators.required],
      dir_departamento: ['', Validators.required],
      dir_codigo_postal: ['', Validators.required],
      ped_estado: ['', Validators.required]
    });
  }

  ngOnInit(): void {
    this.cartSubscription = this.productoServices.cartItems.subscribe(cartItems => {
      this.products = cartItems;
    });

  }

  public get getTotal(): Observable<number> {
    this.totalItems$ = of(this.productoServices.getCartTotalAmount());
    return this.totalItems$;
  }


  // Mtodo para guardar datos del cliente y direccin
  storeOrderData(): void {
    // Obtener los datos del formulario
    const clienteDto = {
      cli_tipo_identificacion: this.checkoutForm.get('cli_tipo_identificacion').value,
      cli_identificacion: this.checkoutForm.get('cli_identificacion').value,
      cli_nombre: this.checkoutForm.get('cli_nombre').value,
      cli_apellido: this.checkoutForm.get('cli_apellido').value,
      cli_telefono: this.checkoutForm.get('cli_telefono').value,
      cli_email: this.checkoutForm.get('cli_email').value,
      //Enviar el ped_estado por DtoCliente
      ped_estado: this.checkoutForm.get('ped_estado').value
    };

    console.log(clienteDto)

    const direccionDto = {
      dir_direccion: this.checkoutForm.get('dir_direccion').value,
      dir_pais: this.checkoutForm.get('dir_pais').value,
      dir_ciudad: this.checkoutForm.get('dir_ciudad').value,
      dir_departamento: this.checkoutForm.get('dir_departamento').value,
      dir_codigo_postal: this.checkoutForm.get('dir_codigo_postal').value
    };

    const detalleDtos = this.products.map(product => ({
      det_pedido_cantidad: product.quantity,
      det_precio_unitario: product.prod_precio,
      det_talla_producto: product.selectedSize || null,
      prod_id: product.prod_id
    }));

    // Almacenar en localStorage
    localStorage.setItem('orderData', JSON.stringify({ clienteDto, direccionDto, detalleDtos }));
  }


  procesarPago(): void {

    // Configurar los atributos y abrir el checkout
    const handler = (window as any).ePayco.checkout.configure({
      key: this.epayco_public_key,
      test: false // Cambia a false para produccin
    });

    //Obtener el valor del carrito
    const total = this.productoServices.getCartTotalAmount();
    
    //Informaion del cliente para enviar a ePayco
    const direccion = this.checkoutForm.get('dir_direccion').value;
    const nombres = this.checkoutForm.get('cli_nombre').value;
    const apellidos = this.checkoutForm.get('cli_apellido').value;
    const telefono = this.checkoutForm.get('cli_telefono').value;

    const nombre_completo = nombres + ' ' + apellidos

    const data = {
      // Configuracin de la transaccin
      name: "Carrito Artesanias Lumar",
      currency: "cop",
      amount: total,
      country: "co",
      lang: "es",
      external: "false",
      response: `${this.urlFront}shop/checkout/success`,
      name_billing: nombre_completo,
      address_billing: direccion,
      type_doc_billing: "cc",
      mobilephone_billing: telefono,
    };

    this.storeOrderData();
    handler.open(data);
  }
}
